










































export default {
	data() {
		return {
			articleList: [],
			keyword: '',
			loadMoreText: "加载中...",
			showLoadMore: false,
			page: 1
		};
	},
	onLoad(params) {
		uni.showLoading({
			title: '加载中',
		});
		this.getList();
	},
	// 监听页面卸载
	onUnload() {
		this.articleList = [],
		this.loadMoreText = "加载更多.....",
		this.showLoadMore = false;
	},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		this.getList();
		
	},
	methods: {
		//调取文章列表
		getList() {
			uni.request({
				url: this.apiUrl + 'article/getArticleList',
				method: 'GET',
				data: {
					page: this.page,
					title: this.keyword
				},
				success: (result) => {
					if (result.data.code == 0) {
						this.loadMoreText = "加载更多.....";
						this.showLoadMore = true;
						if (Array.isArray(result.data.data) && result.data.data.length === 0) {
							this.loadMoreText = "没有更多数据了!"
							return;
						}
						if( result.data.data.length < 10){
							this.loadMoreText = "没有更多数据了!"
						}
						this.page++;
						result.data.data.forEach((article) => {
							this.articleList.push(article);
						});
					}
				},
				fail: () => {
					uni.showToast({
						title: "获取数据失败",
						icon: "none"
					})
				},
				complete: function() {
					uni.hideLoading();
				}

			});
		},
		// 返回上级跳转页面
		navigateBack() {
			uni.navigateBack();
		},
		
		setKeyword(){
			this.loadMoreText = "正在加载.....";
			this.articleList = [];
			this.page = 1;
			this.getList();
		}
	},
}
